<template>
  <div :class="classObj" class="adminTemplate">
    <div class="main-container">
      <el-row class="dr-datatable">
        <el-col :span="24">
          <TopBar :code="codetxt" :path="codePath" type="adminTemplate"></TopBar>
          <el-row :gutter="20">
            <el-col :xs="24" :md="4">
              <div class="grid-content bg-purple">
                <TemplateTree
                  :treeData="adminTemplateList.docs"
                  pageType="adminTemplate"
                  @changeTreeFile="readTreeFile"
                ></TemplateTree>
              </div>
            </el-col>
            <el-col :xs="24" :md="20">
              <div class="in-coder-panel">
                <textarea ref="textarea"></textarea>
              </div>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import TemplateTree from "./templateTree";
import TopBar from "../common/TopBar.vue";
import { mapGetters, mapActions } from "vuex";
// 
import _CodeMirror from "codemirror";
import { initEvent } from "@root/publicMethods/events";

// 
// codemirror  /addon/mode/loadmode.js  /mode/meta.js 
//  vue  JS  JS 
import "codemirror/mode/javascript/javascript.js";
import "codemirror/mode/css/css.js";
// import "codemirror/mode/xml/xml.js";
// import "codemirror/mode/clike/clike.js";
// import "codemirror/mode/markdown/markdown.js";
// import "codemirror/mode/python/python.js";
// import "codemirror/mode/r/r.js";
// import "codemirror/mode/shell/shell.js";
// import "codemirror/mode/sql/sql.js";
// import "codemirror/mode/swift/swift.js";
// import "codemirror/mode/vue/vue.js";

// 
const CodeMirror = window.CodeMirror || _CodeMirror;
export default {
  name: "index",
  props: {
    // 
    value: String,
    // 
    language: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      sidebarOpened: true,
      device: "desktop",
      codetxt: "",
      codePath: "",
      // 
      mode: "html",
      // 
      coder: null,
      // 
      options: {
        // 
        tabSize: 2,
        //  JS 
        theme: "default",
        // 
        lineNumbers: true,
        line: true
      },
      //  JS 
      //  MIME-TYPE  text/ 
      modes: [
        {
          value: "css",
          label: "CSS"
        },
        {
          value: "javascript",
          label: "Javascript"
        },
        {
          value: "html",
          label: "XML/HTML"
        },
        {
          value: "x-java",
          label: "Java"
        },
        {
          value: "x-objectivec",
          label: "Objective-C"
        },
        {
          value: "x-python",
          label: "Python"
        },
        {
          value: "x-rsrc",
          label: "R"
        },
        {
          value: "x-sh",
          label: "Shell"
        },
        {
          value: "x-sql",
          label: "SQL"
        },
        {
          value: "x-swift",
          label: "Swift"
        },
        {
          value: "x-vue",
          label: "Vue"
        },
        {
          value: "markdown",
          label: "Markdown"
        }
      ]
    };
  },
  components: {
    TopBar,
    TemplateTree
  },
  methods: {
    readTreeFile(data) {
      this.codetxt = data.doc;
      this.codePath = data.path;

      if (this.codetxt) {
        this.coder.setValue(this.codetxt);
      }
    },
    // 
    _initialize() {
      // 
      this.coder = CodeMirror.fromTextArea(this.$refs.textarea, this.options);
      // 
      this.coder.setValue(this.value || this.codetxt);

      // 
      this.coder.on("change", coder => {
        this.codetxt = coder.getValue();

        if (this.$emit) {
          this.$emit("input", this.codetxt);
        }
      });

      // 
      if (this.language) {
        // 
        let modeObj = this._getLanguage(this.language);

        // 
        if (modeObj) {
          this.mode = modeObj.label;
        }
      }
    },
    // 
    _getLanguage(language) {
      // 
      return this.modes.find(mode => {
        // 
        let currentLanguage = language.toLowerCase();
        let currentLabel = mode.label.toLowerCase();
        let currentValue = mode.value.toLowerCase();

        //  java  x-java  value  label 
        return (
          currentLabel === currentLanguage || currentValue === currentLanguage
        );
      });
    },
    // 
    changeMode(val) {
      // 
      this.coder.setOption("mode", `text/${val}`);

      // 
      let label = this._getLanguage(val).label.toLowerCase();

      // 
      this.$emit("language-change", label);
    }
  },
  computed: {
    ...mapGetters(["adminTemplateList"]),
    classObj() {
      return {
        hideSidebar: !this.sidebarOpened,
        openSidebar: this.sidebarOpened,
        withoutAnimation: "false",
        mobile: this.device === "mobile"
      };
    }
  },
  mounted() {
    initEvent(this);
    this.$store.dispatch("contentTemp/getAdminTemplateList");
    // 
    this._initialize();
  }
};
</script>

<style lang="scss">
.CodeMirror {
  height: 700px;
  border: 1px solid #eee;
  box-shadow: 1px 1px 1px #eee;
}
</style>